<template>
  <div>
     <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/my/myclient' }">我的客户</el-breadcrumb-item>
          <el-breadcrumb-item>客户详情</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图 -->
        <el-card>
          <div slot="header" class="clearfix">
            <span class="firstSpan">{{customerDetail.customerName}}</span>
            <span class="secondSpan"><i class="el-icon-phone-outline"  style="padding-right:10px;"></i>{{customerDetail.contactPersonName}} / 电话： {{customerDetail.contactPersonPhone?customerDetail.contactPersonPhone:'无 '}} /  微信： {{customerDetail.contactPersonWechat?customerDetail.contactPersonWechat:'无 '}} /  阿里旺旺： {{customerDetail.contactPersonWangwang?customerDetail.contactPersonWangwang:'无 '}}</span>
            <el-button style="float: right; padding: 3px 0;margin: 13px 20px " type="text" class="removeButton" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button>
            <el-button style="float: right; padding: 3px 0;margin: 13px 20px " type="text" class="removeButton" @click="removeButtonHandle">移除</el-button>
            <el-dropdown @command="editHandleCommand" style="float: right; padding: 3px 0;margin: 10px 20px " trigger="click">
             <span class="el-dropdown-link">
               {{customerStatusName}}<i class="el-icon-arrow-down el-icon--right"></i>
             </span>
             <el-dropdown-menu slot="dropdown">
               <el-dropdown-item command="1">跟进中</el-dropdown-item>
               <el-dropdown-item command="2">已成交</el-dropdown-item>
               <el-dropdown-item command="3">已失效</el-dropdown-item>
             </el-dropdown-menu>
            </el-dropdown>
            <!-- customerStatusName -->

          </div>
          <el-row :gutter="40" class="upContentMain" >
            <el-col :span="15">
              <el-row class="commentRow">
                <div class="firstDiv">
                  <span style="float: left; padding: 3px 0;margin: 10px 20px; font-size:18px;">最近一次沟通</span>
                  <span style="float: right; padding: 3px 0;margin: 12px 20px;font-size:14px; color: #f5b53e;" v-if="isUpContentMain" >{{customerDetail.followUpDate}}</span>
                </div>
                <div class="lastDiv" v-if="isUpContentMain" >
                  <span style="padding: 3px 0;margin: 10px -5px 10px 20px; font-size:14px;">{{customerDetail.followUpTheme}}</span>
                  <!-- <a style=" padding: 3px 0;margin: 12px 20px;font-size:14px; " :href="UpdateCustomerDetailUrl" >...详情</a> -->
                </div>
                <div class="lastDiv" v-else>
                  <span style="float: right; padding: 3px 0;margin: 12px 20px;font-size:14px; color: #78b9ee;">暂无跟进....</span>
                </div>
              </el-row>
              <el-row class="commentRow">
                <div class="firstDiv">
                  <span style="float: left; padding: 3px 0;margin: 10px 20px; font-size:18px;">下一次跟进提醒</span>
                  <span style="float: right; padding: 3px 0;margin: 12px 20px;font-size:14px; color: #f5b53e;" v-if="isUpContentMain">{{customerDetail.nextRemindDate}}</span>
                </div>
                <div class="lastDiv" v-if="isUpContentMain">
                  <span style="padding: 3px 0;margin: 10px -5px 10px 20px; font-size:14px;">{{customerDetail.nextFollowUpTheme}}</span>
                  <!-- <a style=" padding: 3px 0;margin: 12px 20px;font-size:14px; " :href="UpdateCustomerDetailUrl1" >...详情</a> -->
                </div>
                <div class="lastDiv" v-else>
                  <span style="float: right; padding: 3px 0;margin: 12px 20px;font-size:14px; color: #78b9ee;">暂无跟进....</span>
                </div>
              </el-row>
            </el-col>
            <el-col :span="9">
              <div class="timeLineTitle">客户跟进与沟通记录</div>

              <el-timeline v-if="isUpContentMain">
                <el-timeline-item     v-for="(item,index) in timeLineContent"  :key="index"  :timestamp="item.followUpRecordUpdateDate" placement="top" size="normal" color="#0099FF" >
                  <el-card>
                    <h4>
                      更进方式:
                      <el-tag type="danger" v-if="item.followUpRecordType===0">电话</el-tag>
                      <el-tag v-else-if="item.followUpRecordType===1">微信</el-tag>
                      <el-tag  type="info" v-else>阿里旺旺</el-tag>
                    </h4>
                    <h4>跟进内容: <p class="upcontent" v-html="item.followUpContent"></p></h4>
                  </el-card>
                </el-timeline-item>
              </el-timeline>
              <div class="lastDiv" v-else>
                  <span style="font-size:14px; color: #78b9ee;">暂无跟进....</span>
                </div>
            </el-col>
          </el-row>
        </el-card>
  </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    return {
      // 客户详情
      customerDetail: {},
      customerId: '',
      UpdateCustomerDetailUrl: '',
      UpdateCustomerDetailUrl1: '',
      timeLineContent: [],
      isUpContentMain: false,
      customerStatusName: '',
      nextRemindDate: ''
    }
  },
  created () {
    this.getCustomerDetail()
  },
  methods: {
    // 点击返回触发该事件
    returnHandle () {
      // console.log(1)
      this.$router.go(-1)
    },
    // 修改客户状态触发该事件
    async editHandleCommand (val) {
      if (val) {
        if (this.customerStatusName !== '已成交') {
          if (val === '1') {
            this.customerStatusName = '跟进中'
          } else if (val === '2') {
            this.customerStatusName = '已成交'
          } else {
            this.customerStatusName = '已失效'
          }
        }
        const data = Qs.stringify({ customerStatusId: val + '', userId: window.sessionStorage.getItem('userId'), customerId: this.$route.params.id, customerStatusName: this.customerStatusName })
        const { data: res } = await this.$http.post('/customer/updateCustomer', data)
        if (res.status !== 200) {
          this.$message.error(res.msg)
        } else {
          this.$router.push('/my/myclient')
          return this.$message.success(res.msg)
        }
      }
    },
    async getCustomerDetail () {
      const data = Qs.stringify({ customerId: this.$route.params.id })
      const { data: res } = await this.$http.post('/customer/findCustomerById', data)
      if (res.status !== 200) return this.$message.error(res.msg)
      if (res.data.followUpRecordWithBLOBsList.length > 0) {
        this.isUpContentMain = true
      }
      // nextRemindDate

      if (res.data.followUpRecordWithBLOBsList.length > 0) {
        const arr = []
        res.data.followUpRecordWithBLOBsList.forEach((item, index) => {
          // const date = Number(new Date(item.nextRemindDate))
          const date = new Date(item.nextRemindDate).valueOf()
          // const date = item.nextRemindDate
          // console.log(date)
          arr.push(date)
        })
        // console.log(res.data.followUpRecordWithBLOBsList)
        // const nowData = Number(new Date())
        for (let i = 0; i < arr.length; i++) {
          for (let j = 0; j < arr.length - 1 - i; j++) {
            if (arr[ j ] > arr[j + 1]) {
              var temp = arr[ j ]
              arr[ j ] = arr[ j + 1 ]
              arr[ j + 1 ] = temp
            }
          }
        }
        // console.log(arr[0])
        // console.log(Number(new Date(arr[0])))
        this.nextRemindDate = this.timeChange(arr[0])
        console.log(this.nextRemindDate)
      }
      this.customerStatusName = res.data.customer.customerStatusName
      this.customerDetail = {
        customerName: res.data.customer.customerName || '',
        contactPersonName: res.data.contactPersonList.length > 0 ? res.data.contactPersonList[0].contactPersonName : '',
        contactPersonWangwang: res.data.contactPersonList.length > 0 ? res.data.contactPersonList[0].contactPersonWangwang : '',
        contactPersonPhone: res.data.contactPersonList.length > 0 ? res.data.contactPersonList[0].contactPersonPhone : '',
        contactPersonWechat: res.data.contactPersonList.length > 0 ? res.data.contactPersonList[0].contactPersonWechat : '',
        followUpTheme: res.data.followUpRecordWithBLOBsList.length > 0 ? res.data.followUpRecordWithBLOBsList[0].followUpTheme : '',
        followUpDate: res.data.followUpRecordWithBLOBsList.length > 0 ? res.data.followUpRecordWithBLOBsList[0].followUpDate.split(' ')[0] : '',
        nextFollowUpTheme: res.data.followUpRecordWithBLOBsList.length > 0 ? res.data.followUpRecordWithBLOBsList[0].nextFollowUpTheme : '',
        nextRemindDate: res.data.followUpRecordWithBLOBsList.length > 0 ? this.nextRemindDate.split(' ')[0] : ''
      }
      // this.customerId = res.data.customer.customerId
      this.timeLineContent = res.data.followUpRecordWithBLOBsList
      this.UpdateCustomerDetailUrl = '/detail/UpdateCustomerDetail?id=' + res.data.customer.customerId
      this.UpdateCustomerDetailUrl1 = '/detail/UpdateCustomerDetail?id=' + res.data.customer.customerId
    },
    // 改变时间格式
    timeChange (time) {
      // var time1 = time.getTime()
      console.log(typeof time)
      var str = time + ''
      var date = new Date(Date.parse(str.replace(/-/g, '/')))
      // console.log(date)
      var Y = date.getFullYear() + '-'
      // console.log(Y)
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
      // console.log(M)
      var D = date.getDate() + ' '
      // console.log(D)
      var h = date.getHours() + ':'
      var m = date.getMinutes() + ':'
      var s = date.getSeconds()
      var current = Y + M + D + h + m + s
      // this.ruleForm.finallyDate = current
      // console.log(current)
      return current
    },
    // 点击修改按钮触发该事件
    editButtonHandle () {
      this.$router.push('/detail/editDetail' + this.$route.params.id)
    },
    // 点击移除按钮触发该事件
    removeButtonHandle () {
      this.$confirm('此操作将客户移入公海, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        // customerPublicAreaStatus
        const data = Qs.stringify({ customerId: this.$route.params.id, userId: window.sessionStorage.getItem('userId'), customerPublicAreaStatus: 0 })
        const { data: res } = await this.$http.post('/customer/updateCustomer', data)
        if (res.status !== 200) return this.$message.error(res.msg)
        // this.$message({
        //   type: 'success',
        //   message: '删除成功!'
        // })
        this.$router.push('/my/myclient')
        return this.$message.success(res.msg)
      }).catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: '已取消删除'
        // })
        this.$router.push({ path: '/my/myclient' })
        // console.log('已取消删除')
      })
    }
  }
}
</script>
<style lang="less" scoped>
.clearfix {
  color: #000000;
  .firstSpan{
    font-weight: 700;
    font-size: 25px;
    margin-right: 5px;
  }
  .secondSpan{
    margin: 0 20px;
    font-size: 14px;
    color: #b3b0b0
  }
}
.commentRow{
  border:1px solid #eee;
  height: 150px;
  margin-bottom: 20px;
  .firstDiv,
  .lastDiv{
    height: 50px;
    // background-color: pink;
    margin: 25px 0;
    a{
      font-size: 14px;
      color: #f5b53e;
    }
  }
}
.timeLineTitle{
  margin-bottom: 20px;
  font-size: 18px;
  color: #606266;
}
.upcontent{
  font-weight: normal;
  padding-left: 65px;
  margin-top: -5px;
}
</style>
