<template>
    <div>
         <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item >我的客户</el-breadcrumb-item>
          <el-breadcrumb-item>客户跟进</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区 -->
        <el-card>
            <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>跟进详情</span>
               <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button>
            </div>
            <div class="cardContent">
                <!-- <el-row>
                  <el-button  style="float: right;" type="info" plain size="mini">删除</el-button>
                </el-row> -->
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">客户名称</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight textRightColor">{{currentCustomer.customerName}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">跟进类型</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">
                         <el-tag type="danger" v-if="currentCustomer.followUpRecordType===0">电话</el-tag>
                        <el-tag v-else-if="currentCustomer.followUpRecordType===1">微信</el-tag>
                        <el-tag  type="info" v-else>阿里旺旺</el-tag>
                      </span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">跟进时间</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight textRightColor">{{currentCustomer.followUpDate}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">主题</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{currentCustomer.followUpTheme}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">跟进内容</span>
                  </el-col>
                   <el-col :span="12">
                      <div class="textRight textRightContent" v-html="currentCustomer.followUpContent"></div>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">下次提醒时间</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight textRightColor">{{currentCustomer.nextRemindDate}}</span>
                  </el-col>
                </el-row>
                  <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">下次跟进主题</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{currentCustomer.nextFollowUpTheme}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">下次跟进内容</span>
                  </el-col>
                   <el-col :span="12">
                      <div class="textRight textRightContent" v-html="currentCustomer.nextFollowUpContent"></div>
                  </el-col>
                </el-row>
            </div>
        </el-card>
    </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    return {
      currentCustomer: {}
    }
  },
  created () {
    this.getCustomerDetail()
    // /customer/findCustomerById
  },
  methods: {
    // 获取当前客户详情
    async getCustomerDetail () {
      const data = Qs.stringify({ customerId: this.$route.query.id })
      const { data: res } = await this.$http.post('/customer/findCustomerById', data)
      if (res.status !== 200) return this.$message.error('获取详情失败！')
      this.currentCustomer = {
        customerName: res.data.customer.customerName,
        followUpRecordType: res.data.followUpRecordWithBLOBsList[0].followUpRecordType,
        followUpDate: res.data.followUpRecordWithBLOBsList[0].followUpDate.split(' ')[0],
        followUpTheme: res.data.followUpRecordWithBLOBsList[0].followUpTheme,
        followUpContent: res.data.followUpRecordWithBLOBsList[0].followUpContent,
        nextRemindDate: res.data.followUpRecordWithBLOBsList[0].nextRemindDate.split(' ')[0],
        nextFollowUpTheme: res.data.followUpRecordWithBLOBsList[0].nextFollowUpTheme,
        nextFollowUpContent: res.data.followUpRecordWithBLOBsList[0].nextFollowUpContent
      }
    },
    // 点击返回按钮 触发该事件
    returnUpadataHandle () {
      this.$router.push('/update/customerUpdate')
    },
    // 点击返回触发该事件，返回上一页
    returnHandle () {
      this.$router.push('/detail/customerDetail' + this.$route.query.id)
    }
  }
}
</script>

<style lang="less" scoped>
// 左侧文本颜色
@textLeftColor: #949393;
// 右侧文本着重变色
@textRightColor: #fdb42d;
.textLeft{
    position: relative;
    top:0 ;
    left: 330px;
    font-size: 16px;
    color: @textLeftColor;
}
.textRight{
    position: relative;
    top: 0;
    right: 100px;
    font-size: 14px;
}
.textRightColor{
    color: @textRightColor;
}
.el-row {
    margin-bottom: 20px;
}
.textRightContent{
  width: 300px;
  border-radius: 5px;
  height: 100%;
  padding: 20px 10px;
  border: 1px solid #cccccc;
  line-height: 20px;;
}
</style>
